.login {
  margin: 0 auto;
  max-width: 358px;
  width: 100%;
}

.login__title {
  text-align: center;
  min-height: 28px;
  margin: 0 0 50px;
  color: #fff;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.21;
}

.login__subtitle {
  margin: 15px 0 0 0;
  text-align: center;
  color: #fff;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
}

.login__subtitle-link {
  text-decoration: none;
  color: #fff;
}
