.login__input {
  width: 100%;
  padding: 0 0 13px 0;
  margin: 0;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #cccccc;
  color: #cccccc;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
  outline: none;
}

.login__input_invalid {
  border-bottom: 2px solid #ff0000;
}

.popup__error {
  display: block;
  color: #ff0000;
  font-weight: 400;
  font-size: 12px;
  line-height: 14.52px;
  min-height: 29.04px;
}

.popup__error:last-of-type {
  margin-bottom: 10px;
}

.login__error {
  display: block;
  color: #ff0000;
  font-weight: 400;
  font-size: 12px;
  line-height: 14.52px;
  min-height: 29.04px;
}

.login__error:last-of-type {
  margin-bottom: 175px;
}
